body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: radial-gradient(circle at 10% 20%, rgb(255, 156, 156) 0%, rgb(179, 201, 241) 90%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.main {
  margin: 0 auto;
  max-width: 500px;
}
.main h1{
  text-align: center;
  color: darkorchid;
  font-size: 50px;
  font-family:Verdana, Geneva, Tahoma, sans-serif
}
.content {
  border: 5px #c0c0ff solid;
  border-radius: 5px;
  font-size: 20px;
  background-color: lightgrey;
  padding: 20px;
}
table {
  width: 100%;
}
td:first-child {
  width: 100%;
}
td:last-child {
  min-width: 125px;
}
th {
  text-align: left;
}
tr:nth-child(even) {
  background-color: white;
}
input,
button {
  font-size: 20px;
  padding: 5px;
  margin: 0px 1px ;
  border-radius: 5px;
  border: 2px #c0c0c0 solid;
}
button {
  cursor: pointer;
}
button.add {
  background-color: #5254d8;
  color: white;
}
button.edit {
  background-color: #20a0b0;
  color: white;
}
button.delete {
  background-color:#da4949 ;
  color: white;
}
button.save {
  background-color: rgb(57, 180, 57);
  color: white;
}
button.back {
  background-color: rgb(223, 202, 202);
  color: white;
}
.task {
  cursor: pointer;
}
.completed {
  text-decoration: line-through;
}
